.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color: white;
}

.login__logo {
    margin: 20px auto;
    width: 100px;
    object-fit: contain;
}

.login__container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login__container > h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.login__container > form > h5 {
    margin-bottom: 5px;
}

.login__container > form > input {
    height: 30px;
    width: 98%;
    margin-bottom: 10px;
    background-color: white;
}

.login__container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login__signInBtn {
    width: 100%;
    height: 30px;
    background: #f0c14b;
    border-radius: 2px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}

.login__regBtn {
    width: 100%;
    height: 30px;
    border-radius: 2px;
    border: 1px solid;
    border-color: darkgray;
    margin-top: 10px;
}